@import "../App/layout.scss";

.item {
  margin-bottom: 20px;
  padding: 20px 10px;

  background-color: #F7F7F7;

  font-size: 1rem;

  &:last-of-type {
    margin-bottom: 0;
  }

}

.author {
  font-weight: 600;
  text-align: left;
}

.date {
  display: block;
  padding: 10px 0;

  font-size: 1rem;
}

.rating {
  padding: 10px 0;
}

.text {
  font-size: 1rem;
  line-height: 1.4rem;
}

@include tablet-portrait-up {
  .item, .text {
    font-size: 1.2rem;
  }
}

  @include tablet-portrait-up() {
    .date {
      display: inline-block;
      margin-left: 50px;
    }
  }



